<template>
    <div class='app-home'>
        <div class='header'>
            <ul>
                <li>音乐首页</li>
                <li class='header-active'>正在播放</li>
                <li>播放列表</li>
                <li>歌曲搜索</li>
            </ul>
        </div>
        <div id='cont' class='content'>
            <canvas id='can'></canvas>
        </div>
        <div class='music-info'>
            <div class='music-header'>
                <p>只要平凡</p>
                <p v-html='subm'></p>
            </div>
            <img id='ming' src='../../img/m.jpg'>
            <div class='music-words'>
                <div id='wcon'>
                    <p v-for='(w,i) in words' :key='i' v-cloak>{{w.slice(w.lastIndexOf(']')+1)}}</p>
                </div>
            </div>
        </div>
        <div class='operate'>
            <ul>
                <li><i class='fa fa-heart'></i></li>
                <li><i class='fa fa-download'></i></li>
                <li><i class='fa fa-share-alt'></i></li>
                <li><i class='fa fa-ellipsis-v'></i></li>
            </ul>
        </div>
        <div class='footer'>
          <div class='progress' @click.self='clickProgress($event)'>
            <div @click.self='clickProgress($event)'></div>
            <i class='fa fa-circle'></i>
          </div>
          <div class='showtime' v-cloak>
             {{$store.getters.getNtime|dateFilter}}
          </div>
          <div v-if='isPlay' class='endtime' v-cloak>
             {{$store.getters.getMtime|dateFilter}}
          </div>
          <div v-else class='endtime' v-cloak>
            00:00
          </div>
       </div>
       <div class='controls'>
            <div>
                <span><i class='fa fa-random'></i></span>
            </div>
            <div>
                <span><i class='fa fa-step-backward'></i></span>
                <span><i id='cons' @click='musicPause($event)' class='fa fa-play pp'></i></span>
                <span><i class='fa fa-step-forward'></i></span>
            </div>
            <div>
                <span><i class='fa fa-navicon'></i></span>
            </div>
        </div>
        <audio id='mm' :src='mid' muted='false' preload='auto'></audio>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                backImage:'',
                w:0,
                n:0,
                deg:0,
                isPlay:false,
                subm:'张杰/张碧晨-只要平凡(《我不是药神》- 主题曲) ',
                timer:null,
                timer2:null,
                timer3:null,
                timer4:null,
                fname:1,
                words:[],
                mid:'',
                ctop:0
            }
        },
        methods:{
            drawBackground(){
                var can = document.getElementById('can')
                can.width = window.innerWidth
                can.height = window.innerHeight
                var ct = can.getContext('2d')
                var p = new Image()
                p.src = this.backImage;
                p.onload = function (){ 
                    var x=Math.floor(Math.random()*can.width+50),y=Math.floor(Math.random()*can.height/2)
                    var tim = setInterval(drawStartRain,20);
                    function drawStartRain(){  
                        ct.clearRect(0,0,can.width,can.height)
                        ct.drawImage(p,0,0,can.width,can.height);
                        ct.beginPath()
                        ct.strokeStyle='white'
                        ct.moveTo(x,y)
                        ct.lineTo(x-50,y+50)
                        ct.stroke()
                        x -= 10;
                        y += 10  
                        if(x<=-10){
                            clearInterval(tim);
                            setTimeout(()=>{
                                x=Math.floor(Math.random()*can.width+50)
                                y=Math.floor(Math.random()*can.height/2)
                                tim = setInterval(drawStartRain,20)
                            },2000)
                        }    
                    }
                }
            },
            getBgImg(){
                this.axios.get('http://127.0.0.1:3005/image/bg').then(res=>{
                    this.backImage = res.data 
                    this.drawBackground()
                    var mm = document.getElementById('mm')
                    mm.onplay = ()=>{ 
                        this.isPlay=true;
                        this.$store.commit('chgMtime',mm.duration*1000)
                    }   
                })
            },
            //图片旋转
            roundImg(){
                this.deg = this.deg + 5
                var ming = document.getElementById('ming')
                ming.style.transform = `rotate(${this.deg}deg)`
            },
            //读取进度条
            moveProgress(){
                var prog = document.querySelector('div.progress>div') 
                var pcir = prog.nextElementSibling
                var pw = prog.parentElement.offsetWidth
                var stime = document.querySelector('div.showtime') 
                if(this.w>=pw){
                    var cons = document.getElementById('cons')
                    cons.className = 'fa fa-play pp'
                    clearInterval(this.timer)
                    clearInterval(this.timer2)
                    clearInterval(this.timer4)
                    return;
                }
                this.w += pw/(this.$store.getters.getMtime/1000)
                prog.style.width = this.w + 'px'
                if(this.w > pcir.offsetWidth/2){
                     pcir.style.left = this.w - pcir.offsetWidth/2 + 'px'
                }
            },
            //点击进度条快进、后退
            clickProgress(e){
                if(this.isPlay){
                    var prog = document.querySelector('div.progress>div') 
                    var wcon = document.getElementById('wcon')
                    var pcir = prog.nextElementSibling
                    var pw = prog.parentElement.offsetWidth
                    var mm = document.getElementById('mm')
                    this.w = e.offsetX
                    this.n = (this.w/(pw/(this.$store.getters.getMtime/1000))).toFixed(2)*1000
                    //音频暂停状态也能快进修改时间
                    this.$store.commit('chgNtime',this.n)
                    //音频当前播放时间
                    mm.currentTime = this.w/(pw/(this.$store.getters.getMtime/1000))
                    for(var i=0;i<this.words.length-2;i++){
                        var pre = this.words[i].slice(1,9)
                        pre = parseInt(pre.slice(0,2))*60*1000 + parseInt(pre.slice(3))*1000
                        if(this.words[i+2] != undefined){
                            var bac = this.words[i+1].slice(1,9)
                            bac = parseInt(bac.slice(0,2))*60*1000 + parseInt(bac.slice(3))*1000
                        }
			            if((this.n >= pre && this.n <= bac)){
                            if((this.n - pre) <= (Math.abs(bac - this.n))){
                                this.ctop = -(i)*26
                            }else{
                                this.ctop = -(i+1)*26
                            }
                            wcon.children[i].style.color='#666'
                            wcon.children[i+1].style.color='white'
                            wcon.style.top = this.ctop +'px'
                            break;
                        }
                        if(this.n + 13000  >= mm.duration*1000){
                            this.ctop = -(this.words.length-2)*26
                            wcon.style.top =  this.ctop +'px'
                            wcon.lastElementChild.previousElementSibling.style.color='white'
                        }
                    }
                    prog.style.width = this.w + 'px'
                    if(this.w > pcir.offsetWidth/2){
                        pcir.style.left = this.w - pcir.offsetWidth/2 + 'px'
                    }  
                }  
            },
            //获得歌词
            getWords(){
                this.axios.get(
                    'http://127.0.0.1:3005/image/words',
                    {
                        params:{
                            fname:this.fname
                        }
                    }
                ).then(res=>{
                    this.words = res.data.musicwords.split('\n')
                    this.mid = res.data.id
                })
            },
            timeFilter(val){
                var m = Math.floor(val / 1000 / 60)
                var s = (((val / 1000 / 60) - m)*60).toFixed(2)
                if(m < 10) m = '0' + m 
                if(s < 10) s = '0' + s 
                return `${m}:${s}`
            },
            //歌词同步滚动 10ms执行一次
            moveWords(){
                this.n += 10
                var now = this.timeFilter(this.n)
                var wcon = document.getElementById('wcon')
                for(var i=0;i<this.words.length;i++){
                    if(this.words[i].slice(1,9) == now){		
                        this.ctop = -(i+1)*26
                        if(this.words[i+2]!=undefined){
                            wcon.style.top =  this.ctop +'px'   
                        }          
                        wcon.children[i].style.color='#666'
			wcon.children[i+1].style.color='white'
                    }
		    
                }       
                if(Math.floor(this.n) % 1000 == 0){
                     this.$store.commit('chgNtime',this.n)
                }
                if(this.n >= mm.duration*1000){
                    clearInterval(this.timer3)
                    return;
                }
            },
            musicPause(){
                var mm = document.getElementById('mm')
                var cons = document.getElementById('cons')
                if(mm.paused){
                    mm.play();
                    this.musicSwitchOn()
                    cons.className = 'fa fa-pause'
                }else{
                    mm.pause()
                    this.musicSwitchOff()
                    cons.className = 'fa fa-play pp'
                }
            },
            moveSubm(){
                this.subm = this.subm.slice(1) + this.subm.slice(0,1)
            },
            musicSwitchOn(){
                this.timer = setInterval(this.moveProgress,1000)
                this.timer2 = setInterval(this.roundImg,300)
                this.timer3 = setInterval(this.moveWords,10)
                this.timer4 = setInterval(this.moveSubm,1000)
            },
            musicSwitchOff(){
                clearInterval(this.timer)
                clearInterval(this.timer2)
                clearInterval(this.timer3)
                clearInterval(this.timer4)
            }
        },
        mounted(){
            this.getBgImg()
            this.getWords()
        }
    }
</script>
<style>
    [v-cloak]{display:none;}
    *{margin:0;padding:0;}
    div.app-home .header{
        position:fixed;
        width:100%;
        height:2.5rem;
        top:0;
    }
    div.app-home .header>ul{
        float:left;
        list-style:none;
        width:100%;
        height:2.5rem;
    }
    div.app-home .header>ul>li{
        float:left;
        width:25%;
        height:2.5rem;
        text-align:center;
        line-height:2.5rem;
        background:#1A1C30;
        color:white;
        font-size:0.8rem;
    }
    #cont{position:absolute;z-index:-1;}
    .header-active{
        border-bottom:2px solid white;
    }
    div.app-home>div.content{
        position:fixed;
        top:2.5rem;
        width:100%;
        overflow:hidden;
    }
    div.app-home>div.music-info{
        position:absolute;
        top:4rem;
        left:0;
        width:100%;
        height:16rem;
    }
    div.app-home>div.music-info>div.music-header{
        position:absolute;
        top:0;
        left:50%;
        width:12rem;
        margin-left:-6rem;
        text-align:center;
    }
    div.app-home>div.music-info>div.music-header>p{
        position:relative;
        color:white;
        overflow:hidden;
        white-space:nowrap;
        /*text-overflow:ellipsis;*/
        margin:0;
        letter-spacing:3px;
    }
    div.app-home>div.music-info>div.music-header>p:first-child{
        top:0;
        font-size:1rem;
    }
    div.app-home>div.music-info>div.music-header>p:last-child{
        width:80%;
        left:10%;
        bottom:0;
        overflow:hidden;
        font-size:0.7rem;
    }
    div.app-home>div.music-info>img{
        position:absolute;
        width:10rem;
        height:10rem;
        bottom:1rem;
        left:50%;
        margin-left:-5rem;
        border-radius:50%;
        transition:all 1s linear;
    }
    div.app-home>div.music-info>.music-words{
        position:absolute;
        top:17rem;
        width:80%;
        height:3.2rem;
        left:10%;
        overflow:hidden;
    }
    div.app-home>div.music-info>.music-words>div>p:first-child{color:white;}
    div.app-home>div.music-info>.music-words>div>p{
        height:21px;
        margin-bottom:5px;
        letter-spacing:0.1rem;
        color:#666;
    }
    div.app-home>div.music-info>.music-words>div{
        position:absolute;
        top:0;
        width:100%;
        left:0;
        transition:all 1s;
    }

    div.app-home>div.operate{
        position:fixed;
        bottom:9rem;
        width:60%;
        left:20%;
        text-align:center;
    }
    div.app-home>div.operate>ul{
        list-style:none;
        width:100%;
    }
    div.app-home>div.operate>ul:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-home>div.operate>ul>li{
        float:left;
        color:white;
        width:25%;
        text-align:center;
        font-size:1.2rem;
    }

    div.app-home>div.footer{
        position:fixed;
        width:100%;
        height:2.8rem;
        bottom:6rem;
        line-height:2.8rem;
    }
    div.app-home>.controls{
        position:fixed;
        width:100%;
        left:0;
        bottom:0;
        height:6rem;
    }
    div.app-home>.controls>div:first-child{
        position:absolute;
        color:white;
        top:2.2rem;
        left:1rem;
        font-size:1.2rem;
    }
    div.app-home>.controls>div:last-child{
        position:absolute;
        color:white;
        top:2.2rem;
        right:1rem;
        font-size:1.2rem;
    }
    div.app-home>.controls>div:nth-child(2){
        position:absolute;
        left:50%;
        width:70%;
        height:100%;
        margin-left:-35%;
    }
    div.app-home>.controls>div:nth-child(2)>span{
        position:absolute;
        top:1.35rem;
        width:3rem;
        height:3rem;
        background:rgba(255,255,255,.2);
        text-align:center;
        line-height:3rem;
        border-radius:50px;
        color:white;
        font-size:1.2rem;
    }
    div.app-home>.controls>div:nth-child(2)>span:first-child{left:1rem;}
    div.app-home>.controls>div:nth-child(2)>span:nth-child(2){
        top:0.85rem;
        width:4rem;
        height:4rem;
        line-height:4rem;
        left:50%;
        font-size:1.6rem;
        margin-left:-2rem;
    }
    .pp{
        margin-left:0.4rem;
    }
    div.app-home>.controls>div:nth-child(2)>span:nth-child(3){right:1rem;}
    div.app-home>div.footer>.progress{
        position:absolute;
        top:1.35rem;
        left:16.5%;
        width:66%;
        background:rgba(255,255,255,0.25);
        height:0.2rem;
        border-radius:0.5rem;
    }
    div.app-home>div.footer>.progress>div{
        position:absolute;
        height:100%;
        width:0;
        background:lightblue;
    }
    div.app-home>div.footer>.progress>i{
        position:absolute;
        top:-0.25rem;
        left:0;
        font-size:0.8rem;
        color:white;
    }
    div.app-home>div.footer>.showtime,div.app-home>div.footer>.endtime{
        position:absolute;
        top:0.1rem;
        font-size:0.8rem;
        color:white;
    }
    div.app-home>div.footer>.showtime{left:1rem;}
    div.app-home>div.footer>.endtime{ 
        width:2rem;
        right:1rem;
    }
</style>


